'use client'

import { useState } from 'react'
import { SearchParams, Business } from '@/lib/types'
import SearchForm from '@/components/SearchForm'
import BusinessResults from '@/components/BusinessResults'
import { Card } from '@/components/ui/card'

export default function Home() {
  const [isLoading, setIsLoading] = useState(false)
  const [businesses, setBusinesses] = useState<Business[]>([])
  const [error, setError] = useState<string | null>(null)

  const handleSearch = async (params: SearchParams) => {
    try {
      setIsLoading(true)
      setError(null)

      const queryParams = new URLSearchParams({
        location: params.location,
        ...(params.category && { category: params.category }),
        websiteStatus: params.websiteStatus,
        activityLevel: params.activityLevel,
        ...(params.aiFeatures && { aiFeatures: JSON.stringify(params.aiFeatures) })
      })

      const response = await fetch(`/api/search?${queryParams.toString()}`)
      const data = await response.json()

      if (!data.success) {
        throw new Error(data.message || 'Failed to fetch results')
      }

      setBusinesses(data.data)
    } catch (err) {
      setError(err instanceof Error ? err.message : 'An error occurred')
      setBusinesses([])
    } finally {
      setIsLoading(false)
    }
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <header className="bg-white border-b">
        <div className="max-w-7xl mx-auto px-4 py-6">
          <h1 className="text-3xl font-bold text-gray-900">AI Business Opportunity Finder</h1>
          <p className="mt-2 text-gray-600">
            Discover active businesses that need website development services
          </p>
        </div>
      </header>

      {/* Main Content */}
      <main className="max-w-7xl mx-auto px-4 py-8">
        <div className="space-y-8">
          {/* Search Section */}
          <section>
            <SearchForm onSearch={handleSearch} isLoading={isLoading} />
          </section>

          {/* Results Section */}
          <section>
            {error && (
              <Card className="p-4 mb-6 bg-red-50 border-red-200">
                <p className="text-red-600">{error}</p>
              </Card>
            )}

            {isLoading ? (
              <Card className="p-6">
                <div className="flex items-center justify-center">
                  <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div>
                  <span className="ml-3 text-gray-600">Analyzing businesses with AI...</span>
                </div>
              </Card>
            ) : businesses.length > 0 ? (
              <div>
                <div className="flex justify-between items-center mb-4">
                  <h2 className="text-xl font-semibold text-gray-900">
                    Found {businesses.length} Opportunities
                  </h2>
                </div>
                <BusinessResults businesses={businesses} />
              </div>
            ) : !error && (
              <Card className="p-6">
                <div className="text-center text-gray-600">
                  Enter a location and search parameters to find business opportunities
                </div>
              </Card>
            )}
          </section>
        </div>
      </main>

      {/* Footer */}
      <footer className="bg-white border-t mt-12">
        <div className="max-w-7xl mx-auto px-4 py-6">
          <p className="text-center text-gray-600 text-sm">
            Powered by AI - Find businesses that need your web development services
          </p>
        </div>
      </footer>
    </div>
  )
}
