'use client'

import { useState } from 'react'
import { Business } from '@/lib/types'
import { Card } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Progress } from '@/components/ui/progress'

interface BusinessResultsProps {
  businesses: Business[]
}

export default function BusinessResults({ businesses }: BusinessResultsProps) {
  const [expandedId, setExpandedId] = useState<number | null>(null)

  const toggleExpand = (id: number) => {
    setExpandedId(expandedId === id ? null : id)
  }

  return (
    <div className="space-y-6">
      {businesses.map((business) => (
        <Card key={business.id} className="p-6 hover:shadow-lg transition-shadow">
          {/* Basic Info Section */}
          <div className="flex justify-between items-start">
            <div>
              <h3 className="text-xl font-semibold text-gray-900">{business.name}</h3>
              <p className="text-gray-600 mt-1">{business.address}</p>
              
              <div className="flex items-center mt-2 space-x-4">
                <div className="flex items-center">
                  <span className="text-yellow-400 text-lg">â˜…</span>
                  <span className="ml-1 text-gray-700">{business.rating}</span>
                </div>
                <span className="text-gray-600">{business.reviewCount} reviews</span>
                <span className="text-gray-600">{business.phone}</span>
              </div>
            </div>

            <div className="flex flex-col items-end space-y-2">
              {business.aiAnalysis && (
                <>
                  <span className={`px-3 py-1 rounded-full text-sm font-medium
                    ${business.opportunityScore && business.opportunityScore >= 80 
                      ? 'bg-green-100 text-green-800' 
                      : business.opportunityScore && business.opportunityScore >= 60
                      ? 'bg-yellow-100 text-yellow-800'
                      : 'bg-gray-100 text-gray-800'}`}>
                    {business.opportunityScore}/100 Opportunity
                  </span>
                  <span className={`text-sm ${!business.hasWebsite ? 'text-blue-600' : 'text-orange-600'}`}>
                    {!business.hasWebsite ? 'No Website' : 'Outdated Website'}
                  </span>
                </>
              )}
            </div>
          </div>

          {/* AI Analysis Section */}
          {business.aiAnalysis && (
            <div className="mt-4">
              <div className="flex items-center justify-between">
                <h4 className="text-sm font-semibold text-gray-700">AI Analysis</h4>
                <Button
                  onClick={() => toggleExpand(business.id)}
                  variant="ghost"
                  className="text-blue-600 hover:text-blue-800"
                >
                  {expandedId === business.id ? 'Show Less' : 'Show More'}
                </Button>
              </div>

              {/* Quick Stats */}
              <div className="mt-2 grid grid-cols-1 md:grid-cols-3 gap-4">
                <div className="bg-gray-50 p-3 rounded-lg">
                  <div className="text-sm text-gray-600">Website Status</div>
                  <div className="font-medium mt-1">{business.aiAnalysis.websiteStatus}</div>
                </div>
                <div className="bg-gray-50 p-3 rounded-lg">
                  <div className="text-sm text-gray-600">Activity Level</div>
                  <div className="font-medium mt-1">{business.aiAnalysis.activityLevel}</div>
                </div>
                <div className="bg-gray-50 p-3 rounded-lg">
                  <div className="text-sm text-gray-600">Market Potential</div>
                  <div className="font-medium mt-1">{business.aiAnalysis.marketPotential}</div>
                </div>
              </div>

              {/* Expanded Analysis */}
              {expandedId === business.id && (
                <div className="mt-4 space-y-4">
                  <div className="border-t pt-4">
                    <h5 className="text-sm font-semibold text-gray-700 mb-2">Detailed Analysis</h5>
                    <div className="space-y-2">
                      <p className="text-sm text-gray-600">{business.aiAnalysis.recommendation}</p>
                      <p className="text-sm text-gray-600">{business.aiAnalysis.competitorInsights}</p>
                    </div>
                  </div>

                  <div>
                    <h5 className="text-sm font-semibold text-gray-700 mb-2">Opportunity Score Breakdown</h5>
                    <div className="space-y-3">
                      <div>
                        <div className="flex justify-between text-sm mb-1">
                          <span>Website Modernization Need</span>
                          <span>{business.hasWebsite ? '85%' : '100%'}</span>
                        </div>
                        <Progress value={business.hasWebsite ? 85 : 100} className="h-2" />
                      </div>
                      <div>
                        <div className="flex justify-between text-sm mb-1">
                          <span>Business Activity</span>
                          <span>{business.reviewCount > 50 ? '90%' : '70%'}</span>
                        </div>
                        <Progress value={business.reviewCount > 50 ? 90 : 70} className="h-2" />
                      </div>
                    </div>
                  </div>

                  <div className="flex justify-end pt-4">
                    <Button className="bg-blue-600 text-white hover:bg-blue-700">
                      Export Analysis
                    </Button>
                  </div>
                </div>
              )}
            </div>
          )}
        </Card>
      ))}
    </div>
  )
}
